* {
  font-family: "Open Sans";
  font-weight: 300;
}
.form {
  margin: 0 auto;
  width: 280px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.selectWrapper {
  width: 100%;
  margin-bottom: 10px;
  font-size: 12px;
  line-height: 1;
  height: 40px;
}
.form .input {
  width: 100%;
  padding: 14px 15px;
  font-size: 12px;
  line-height: 1;
  background: #222;
  border: 1px solid #fff;
  color: #fff;
  font-weight: 300;
}
.form input:not(:last-child) {
  margin-bottom: 10px;
}
.form p {
  margin-bottom: 10px;
  font-size: 12px;
  font-weight: 300;
  color: #fff;
  text-align: left;
}
.form .submit {
  display: block;
  align-self: center;
  width: 100%;
  font-size: 14px;
  line-height: 1;
  padding: 13px 0;
  text-transform: uppercase;
  color: #fff;
  font-weight: 300;
  background: #69C9CA;
  border: none;
  cursor: pointer;
  position: relative;
}
.statusTextErr, .statusTextSuccess {
  position: absolute;
  left: 0;
  bottom: -62px;
  font-size: 11px;
  text-align: left;
  display: flex;
  align-items: center;
  line-height: 1.5;
  width: 100%;
  text-transform: none;
  padding: 5px 10px;
  white-space: pre-line;
}
.statusTextSuccess {
  color: #6AC259;
  border: 1px solid #6AC259;
}
.statusTextErr {
  color: #8B2C2C;
  border: 1px solid #8B2C2C;
}
.statusTextErr::before, .statusTextErr::after {
  content: '';
  position: absolute;
  left: 20px; top: -20px;
  border: 10px solid transparent;
  border-bottom: 10px solid #8B2C2C;
 }
 .statusTextErr::after {
  border-bottom: 10px solid #222;
  top: -19px;
 }
 .statusTextSuccess::before, .statusTextSuccess::after {
  content: '';
  position: absolute;
  left: 20px; top: -20px;
  border: 10px solid transparent;
  border-bottom: 10px solid #6AC259;
 }
 .statusTextSuccess::after {
  border-bottom: 10px solid #222;
  top: -19px;
 }
.icon {
  display: block;
  width: auto;
  height: 40px;
  margin-right: 5px;
}

@media(min-width: 1200px) {
  .form {
    width: 360px;
  }
  .form .input {
    font-size: 18px;
    padding: 11px 15px;
  }
  .selectWrapper {
    font-size: 18px;
  }
  .form p {
    font-size: 18px;
  }
  .form .submit {
    text-align: center;
    width: 200px;
    font-size: 18px;
    padding: 18px 0;
    background: none;
    border: 1px solid #69C9CA;
    color: #69C9CA;
    transition: all 0.3s ease;
  }
  .form .submit:hover {
    color: #fff;
    background: #69C9CA;
  }
  .statusTextErr, .statusTextSuccess {
    width: 360px;
    padding: 10px 20px;
    font-size: 14px;
    bottom: -75px;
    left: -81px;
  }
  .statusTextSuccess::before, .statusTextSuccess::after, .statusTextErr::before, .statusTextErr::after {
    left: 90px;
  }
}
