.overlay {
  padding: 0;
}
.modal {
  width: 100%;
  height: 100%;
  background: #222;
  background-image: url('../../img/modalForm/colorMobTop.png'), url('../../img/modalForm/whiteMobBot.png');
  background-image: -webkit-image-set(url('../../img/modalForm/colorMobTop.webp') 1x, url('../../img/modalForm/whiteMobBot.webp') 1x, url('../../img/modalForm/colorMobTop.png') 1x, url('../../img/modalForm/whiteMobBot.png') 1x);
  background-repeat: no-repeat;
  background-size: calc(100vw * 0.45) auto, calc(100vw * 0.4) auto;
  background-position: 0 0, 100% 100%;
}
.logo {
  margin: 48px auto 10px auto;
  display: block;
  width: 72px;
  height: auto;
}
.wrapper i {
  position: absolute;
  top: 20px;
  right: 20px;
  color: #fff;
  font-size: 42px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.wrapper i:hover {
  transform: rotate(90deg);
  color: rgba(105, 201, 202, .4);
}

@media(min-width: 1200px) {
  .overlay {
    padding: 1.2rem;
  }
  .modal {
    width: 440px;
    height: 720px;
    background-image: url('../../img/modalForm/colorDeskTop.png'), url('../../img/modalForm/whiteDeskBot.png');
    background-image: -webkit-image-set(url('../../img/modalForm/colorDeskTop.webp') 1x, url('../../img/modalForm/whiteDeskBot.webp') 1x, url('../../img/modalForm/colorDeskTop.png') 1x, url('../../img/modalForm/whiteDeskBot.png') 1x);
    background-repeat: no-repeat;
    background-size: 190px auto, 160px auto;
    background-position: 0 0, 100% 100%;
  }
  .logo {
    width: 113px;
  }
}
