.selectWrapper__control, .selectWrapper__menu {
  border-radius: 0px !important;
  min-height: 40px !important;
  border: 1px solid #fff !important;
  color: #fff !important;
  background: #222 !important;
}
.selectWrapper__input > input {
  margin-bottom: 0 !important;
  color: #fff !important;
}
.selectWrapper__single-value {
  font-weight: 300;
  color: #fff !important;
}
.selectWrapper__option:defined {
  background: #222;
}
.selectWrapper__option:hover, .selectWrapper__option:active, .selectWrapper__option:focus {
  background: #333;
}
